.box {
  text-decoration: none;
  color: whitesmoke;
  background-color: transparent;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
  align-items: center;
  margin-bottom: 25px;
  transition: all 0.25s ease-in-out;
  border-radius: 8px;
  border-bottom: 1px solid black;
}

.box:hover {
  background-color: #333;
  color: snow;
  cursor: pointer;
}

.H1 {
  font-size: 1rem;
  margin: 0.5rem 0;
  padding: 0;
  color: #8e8787;
}

.insideDiv {
  margin-left: 10px;
  flex-grow: 11;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.outsideDiv {
  margin-right: 30px;
}

.onlineStatusDot {
  width: 30px;

  display: flex;
  justify-content: center;
  align-items: center;
}

small {
  display: block;
  padding: 0 0 10px;
}
.img {
  width: 2rem;
  height: 2rem;

  border-radius: 50%;
}
.add {
  height: 1.5rem;

  width: fit-content;
  background: transparent;
  border: none;
  color: white;
  margin-right: 1rem;
  cursor: pointer;
}
@media screen and (max-width: 1000px) {
  .H1 {
    font-size: 0.8rem;
  }
}
