.section {
  width: 98%;
  height: 75%;
  overflow-y: scroll;
  overflow-x: hidden;
  padding: 10px;
  scroll-behavior: smooth;
  z-index: 0;
  background-image: url(https://i1.wp.com/www.techgrapple.com/wp-content/uploads/2016/07/WhatsApp-Chat-theme-iPhone-2.jpg?fit=744,1392x);
  margin-bottom: 0.5rem;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 8px;
}
.section::-webkit-scrollbar {
  width: 0;
}
.section div {
  z-index: 10;
}

.adminBox {
  width: 100%;
  margin-bottom: 10px;
  display: flex;
  flex-direction: row-reverse;
}

.friendBox {
  width: 100%;
  margin-bottom: 10px;
  display: flex;
  flex-direction: row;
}

.admin {
  background-color: honeydew;
  min-width: 250px;
  max-width: 500px;
  padding: 10px;
  border-radius: 0.5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-shadow: 0 0 5px #777;
}

.admin > span {
  width: 100%;
  color: gray;
  text-align: right;
}

.friend {
  background-color: aliceBlue;
  min-width: 250px;
  max-width: 500px;
  padding: 10px;
  border-radius: 0.5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-shadow: 0 0 5px #777;
}

.friend > span {
  width: 100%;
  color: gray;
  text-align: right;
}
@media screen and (max-width: 1000px) {
  .section {
    height: 84%;
  }
}
@media screen and (max-width: 600px) {
  .section {
    position: absolute;
    left: 0rem;
    top: 4.3rem;
    width: 22rem;
    height: 32rem;
    z-index: 1;
  }
}
@media screen and (max-width: 300px) {
  .section {
    top: 5rem;
    width: 24.5rem;
    height: 46rem;
  }
}
